/* eslint-disable react/no-array-index-key */
/* eslint-disable no-shadow */
import React, { useState, useRef, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Box,
  Button,
  CardMedia,
  TextField,
  Typography,
  Paper,
  makeStyles
} from '@material-ui/core';
import QuillEditor from 'src/components/QuillEditor';
import { SET_DETAILS } from 'src/actions/serviceActions';
import Images from 'src/theme/images';

const useStyles = makeStyles((theme) => ({
  root: {
    width: 700,
  },
  addTab: {
    marginLeft: theme.spacing(2)
  },
  tag: {
    '& + &': {
      marginLeft: theme.spacing(1)
    }
  },
  media: {
    height: 200,
    backgroundSize: 'contain',
  },
  uploadMedia: {
    height: 200,
    backgroundSize: 'cover',
  },
  datePicker: {
    '& + &': {
      marginLeft: theme.spacing(2)
    }
  },
  editor: {
    '& .ql-editor': {
      height: 250
    }
  }
}));

function ProjectDetails({
  className,
  onBack,
  onNext,
  ...rest
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [file, setFile] = useState(null);
  const [cover, setCover] = useState(null);
  const { newService: service, file: oldFile } = useSelector((state) => state.service);
  const pickerInput = useRef();

  useEffect(() => {
    if (service) {
      if (service.title) {
        setTitle(service.title);
      }
      if (service.description) {
        setDescription(service.description);
      }
      if (oldFile) {
        const url = URL.createObjectURL(oldFile);
        setFile(oldFile);
        setCover(url);
      }
    }
  }, []);

  const _onFileInputHandler = (event) => {
    event.preventDefault();
    const newFile = event.target.files[0];
    const url = URL.createObjectURL(newFile);
    setCover(url);
    setFile(newFile);
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    dispatch({
      type: SET_DETAILS,
      payload: {
        title,
        description,
        file,
      },
    });
    onNext();
  };

  return (
    <form
      onSubmit={handleSubmit}
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Typography
        variant="h3"
        color="textPrimary"
      >
        Enter Details
      </Typography>
      <Box p={3}>
        <Box>
          <input
            ref={pickerInput}
            type="file"
            style={{
              display: 'none'
            }}
            onChange={(e) => { _onFileInputHandler(e); }}
          />
          <Box
            onClick={() => {
              pickerInput.current.click();
            }}
          >
            { cover ? (
              <CardMedia
                className={classes.uploadMedia}
                image={cover}
              />
            ) : (
              <CardMedia
                className={classes.media}
                image={Images.AddFileIcon}
              />
            )}
          </Box>
        </Box>
      </Box>
      <Box mt={2}>
        <TextField
          fullWidth
          label="Service Title"
          name="title"
          onChange={(e) => { setTitle(e.target.value); }}
          value={title}
          variant="outlined"
        />
        <Paper
          variant="outlined"
          component={Box}
          mt={2}
        >
          {/* <QuillEditor
            label="Service Description"
            name="description"
            onChange={(value) => { setDescription(value); }}
            value={description}
            className={classes.editor}
          /> */}
          <TextField
            multiline
            name="description"
            label="Service Description"
            rows={6}
            fullWidth
            value={description}
            variant="outlined"
            className={classes.editor}
            onChange={(e) => {
              setDescription(e.target.value);
            }}
          />
        </Paper>
      </Box>
      <Box
        mt={6}
        display="flex"
      >
        {onBack && (
          <Button
            onClick={onBack}
            size="large"
          >
            Previous
          </Button>
        )}
        <Box flexGrow={1} />
        {onNext && (
          <Button
            color="secondary"
            type="submit"
            variant="contained"
            size="large"
          >
            Next
          </Button>
        )}
      </Box>
    </form>
  );
}

ProjectDetails.propTypes = {
  className: PropTypes.string,
  onNext: PropTypes.func,
  onBack: PropTypes.func
};

export default ProjectDetails;
