import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  ReactSortable, Sortable, MultiDrag, Swap
} from 'react-sortablejs';
import {
  Button,
  Container,
  Box,
  makeStyles
} from '@material-ui/core';
import { v4 as uuidv4 } from 'uuid';
import { createService } from 'src/actions/serviceActions';

import CheckItem from './CheckItem';

Sortable.mount(new MultiDrag(), new Swap());

const useStyles = makeStyles(() => ({
  root: {
    width: 700,
  },
  checkList: {
    minHeight: 500,
    position: 'relative',
  },
  // addButton: {
  //   position: 'absolute',
  //   bottom: 20,
  //   right: 30,
  // },
  todo: {
    backgroundColor: 'white'
  },
  description: {
    maxWidth: '400px',
    marginLeft: '5px',
    width: '100%',
  }
}));

function Todos({
  className,
  onBack,
  onComplete,
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [checkList, setCheckList] = useState(null);
  const { newService: service, file } = useSelector((state) => (state.service));

  const handleComplete = () => {
    const data = { service: { ...service, check_list: checkList }, file };
    dispatch(createService(data));
    onComplete();
  };

  return (
    <Box
      className={classes.root}
    >
      <Box
        className={classes.checkList}
      >
        {checkList && (
        <ReactSortable
          multiDrag
          swap
          list={checkList}
          setList={setCheckList}
          className={clsx(classes.root, className)}
        >
          {checkList.map((todo, index) => (
            <CheckItem
              key={todo._id}
              checkItem={todo}
              updateItem={(newTodo) => {
                checkList[index] = newTodo;
                setCheckList(checkList);
              }}
              headings={service.headings}
            />
          ))}
        </ReactSortable>
        )}
        <Box
          display="flex"
          flexDirection="row-reverse"
          mt={2}
        >
          <Button
            color="secondary"
            variant="contained"
            onClick={() => {
              if (!checkList) {
                setCheckList([{
                  _id: uuidv4(),
                  form: 'input',
                  description: '',
                  expanded: true
                }]);
              } else {
                setCheckList([...checkList, {
                  _id: uuidv4(),
                  form: 'input',
                  description: '',
                  expanded: true
                }]);
              }
            }}
          >
            Add To Do
          </Button>
        </Box>
      </Box>
      <Box
        mt={6}
        display="flex"
      >
        {onBack && (
        <Button
          onClick={onBack}
          size="large"
        >
          Previous
        </Button>
        )}
        <Box flexGrow={1} />
        {onComplete && (
          <Button
            color="secondary"
            onClick={() => handleComplete()}
            size="large"
            variant="contained"
          >
            Complete
          </Button>
        )}
      </Box>
    </Box>
  );
}

Todos.propTypes = {
  className: PropTypes.string,
  onBack: PropTypes.func,
  onComplete: PropTypes.func,
};

export default Todos;
