import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { v4 as uuidv4 } from 'uuid';
import {
  Box,
  Typography,
  Button,
  makeStyles
} from '@material-ui/core';
import { SET_HEADINGS } from 'src/actions/serviceActions';
import HeadingDetails from './HeadingDetails';

const useStyles = makeStyles((theme) => ({
  root: {},
  container: {
    minHeight: 500,
  },
  stepButton: {
    '& + &': {
      marginLeft: theme.spacing(2)
    }
  }
}));

function ReportTemplate({
  className,
  onBack,
  onNext,
  ...rest
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [headings, setHeadings] = useState([]);
  const { newService: service } = useSelector((state) => state.service);
  if (!service) {
    onBack();
  }

  useEffect(() => {
    if (service.headings) {
      setHeadings(service.headings);
    }
  }, []);

  // const handleChangeTemplate = (event) => {
  //   const index = event.target.value;
  //   setSelTemplate(reports[index]);
  // };

  const handleCreateHeading = () => {
    setHeadings([...headings, { id: uuidv4(), label: 'New Heading', field: 'label' }]);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    dispatch({
      type: SET_HEADINGS,
      payload: headings,
    });
    onNext();
  };

  return (
    <form
      onSubmit={handleSubmit}
      className={clsx(classes.root, className)}
      {...rest}
    >
      {/* <Typography
        variant="h3"
        color="textPrimary"
      >
        Please select one option
      </Typography> */}
      <Box mt={2}>
        <Box
          display="flex"
          className={classes.container}
        >
          {/* <Grid>
            <TextField
              fullWidth
              name="template"
              onChange={handleChangeTemplate}
              select
              SelectProps={{ native: true }}
              variant="outlined"
            >
              {reports.map((template, index) => (
                <option
                  key={template._id}
                  value={index}
                >
                  {`${template.title}`}
                </option>
              ))}
            </TextField>
          </Grid>
          {selTemplate && (
          <Grid>
            <HeadingItem
              headings={selTemplate.headings}
            />
          </Grid>
          )} */}

          <HeadingDetails
            headings={headings}
            updateHeadings={(newHeadings) => { setHeadings(newHeadings); }}
          />
          <Box>
            <Button
              className={classes.formButton}
              color="secondary"
              variant="contained"
              size="small"
              onClick={handleCreateHeading}
            >
              Create Heading
            </Button>
          </Box>
        </Box>
        <Box
          mt={6}
          display="flex"
        >
          {onBack && (
          <Button
            onClick={onBack}
            size="large"
          >
            Previous
          </Button>
          )}
          <Box flexGrow={1} />
          {onNext && (
          <Button
            color="secondary"
            type="submit"
            variant="contained"
            size="large"
          >
            Next
          </Button>
          )}
        </Box>
      </Box>
    </form>
  );
}

ReportTemplate.propTypes = {
  className: PropTypes.string,
  onNext: PropTypes.func,
  onBack: PropTypes.func
};

export default ReportTemplate;
