import React, { useEffect, useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { Link as RouterLink } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import {
  Avatar,
  Box,
  Breadcrumbs,
  Button,
  Card,
  CardContent,
  Container,
  Grid,
  Link,
  Paper,
  Step,
  StepConnector,
  StepLabel,
  Stepper,
  Typography,
  makeStyles,
  withStyles,
  colors
} from '@material-ui/core';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import {
  Star as StarIcon,
  Briefcase as BriefcaseIcon,
  File as FileIcon,
  Grid as GridIcon,
} from 'react-feather';
import Page from 'src/components/Page';
import { SET_NEW_SERVICE } from 'src/actions/serviceActions';
import ReportTemplate from './ReportTemplate';
// import CustomerDetails from './CustomerDetails';
import ProjectDetails from './ProjectDetails';
import Todos from './Todos';

const steps = [
  {
    label: 'Service Details',
    icon: BriefcaseIcon
  },
  // {
  //   label: 'Customer Detail',
  //   icon: UserIcon
  // },
  {
    label: 'Report Template',
    icon: FileIcon
  },
  {
    label: 'Todo Detail',
    icon: GridIcon
  }
];

const CustomStepConnector = withStyles((theme) => ({
  vertical: {
    marginLeft: 19,
    padding: 0,
  },
  line: {
    borderColor: theme.palette.divider
  }
}))(StepConnector);

const useCustomStepIconStyles = makeStyles((theme) => ({
  root: {},
  active: {
    backgroundColor: theme.palette.secondary.main,
    boxShadow: theme.shadows[10]
  },
  completed: {
    backgroundColor: theme.palette.secondary.main,
  }
}));

function CustomStepIcon({ active, completed, icon }) {
  const classes = useCustomStepIconStyles();

  const Icon = steps[icon - 1].icon;

  return (
    <Avatar
      className={clsx(classes.root, {
        [classes.active]: active,
        [classes.completed]: completed,
      })}
    >
      <Icon size="20" />
    </Avatar>
  );
}

CustomStepIcon.propTypes = {
  active: PropTypes.bool,
  completed: PropTypes.bool,
  icon: PropTypes.number
};

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
  },
  avatar: {
    backgroundColor: colors.red[600]
  },
  container: {
    marginBottom: theme.spacing(1),
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    maxWidth: 1200,
  }
}));

function ServiceCreateView() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [activeStep, setActiveStep] = useState(0);
  const [completed, setCompleted] = useState(false);

  useEffect(() => {
    dispatch({
      type: SET_NEW_SERVICE,
      payload: null,
    });
  }, []);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleComplete = () => {
    setCompleted(true);
  };

  return (
    <Page
      className={classes.root}
      title="Project Create"
    >
      <Box
        mb={2}
        ml={2}
      >
        <Breadcrumbs
          separator={<NavigateNextIcon fontSize="small" />}
          aria-label="breadcrumb"
        >
          <Link
            variant="body1"
            color="inherit"
            to="/app"
            component={RouterLink}
          >
            Dashboard
          </Link>
          <Typography
            variant="body1"
            color="textPrimary"
          >
            Service
          </Typography>
        </Breadcrumbs>
        <Typography
          variant="h3"
          color="textPrimary"
        >
          Create Service
        </Typography>
      </Box>
      {!completed ? (
        <Paper
          className={classes.container}
        >
          <Box
            xs={12}
            md={9}
          >
            <Stepper
              activeStep={activeStep}
              connector={<CustomStepConnector />}
              orientation="horizontal"
              component={Box}
              bgcolor="transparent"
            >
              {steps.map((step) => (
                <Step key={step.label}>
                  <StepLabel StepIconComponent={CustomStepIcon}>
                    {step.label}
                  </StepLabel>
                </Step>
              ))}
            </Stepper>
          </Box>
          <Box
            xs={12}
            md={9}
          >
            <Box
              p={3}
              display="flex"
            >
              {activeStep === 0 && (
                <ProjectDetails
                  onNext={handleNext}
                />
              )}
              {/* {activeStep === 1 && (
                <CustomerDetails
                  onNext={handleNext}
                  onBack={handleBack}
                />
              )} */}
              {activeStep === 1 && (
                <ReportTemplate
                  onNext={handleNext}
                  onBack={handleBack}
                />
              )}
              {activeStep === 2 && (
                <Todos
                  onComplete={handleComplete}
                  onBack={handleBack}
                />
              )}
            </Box>
          </Box>
        </Paper>
      ) : (
        <Card>
          <CardContent>
            <Box
              maxWidth={450}
              mx="auto"
            >
              <Box
                display="flex"
                justifyContent="center"
              >
                <Avatar className={classes.avatar}>
                  <StarIcon />
                </Avatar>
              </Box>
              <Box mt={2}>
                <Typography
                  variant="h3"
                  color="textPrimary"
                  align="center"
                >
                  You are all done!
                </Typography>
              </Box>
              {/* <Box mt={2}>
                <Typography
                  variant="subtitle1"
                  color="textSecondary"
                  align="center"
                >
                  Donec ut augue sed nisi ullamcorper posuere sit amet eu mauris.
                  Ut eget mauris scelerisque.
                </Typography>
              </Box> */}
              <Box
                mt={2}
                display="flex"
                justifyContent="center"
              >
                <Button
                  variant="contained"
                  color="secondary"
                  component={RouterLink}
                  to="/app/services"
                >
                  View your project
                </Button>
              </Box>
            </Box>
          </CardContent>
        </Card>
      )}

    </Page>
  );
}

export default ServiceCreateView;
